import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table, Modal, ModalBody, ModalHeader } from 'reactstrap';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class SurveyList extends Component {
  constructor(props) {
    super(props);

    this.state = {
      surveys: [],
      isLoggedIn: false,
      modalOpen: false,
      age: '',
      email: '',
      name: '',
      q1: '',
      q2: '',
      q3: '',
      q4: '',
      q5: '',
      q6: '',
      q7: '',
      q8: '',
      q9: '',
      q10: '',
      q11: '',
      q12: '',
      q13: '',
      q14: '',
      q15: '',
      q16: '',
    };
    this.fetchServeys = this.fetchServeys.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
  }

  toggleModal(survey) {
    this.setState({
      age: survey.age === undefined ? '' : survey.age,
      email: survey.email === undefined ? '' : survey.email,
      name: survey.name === undefined ? '' : survey.name,
      q1: survey.q1 === undefined ? '' : survey.q1,
      q2: survey.q2 === undefined ? '' : survey.q2,
      q3: survey.q3 === undefined ? '' : survey.q3,
      q4: survey.q4 === undefined ? '' : survey.q4,
      q5: survey.q5 === undefined ? '' : survey.q5,
      q6: survey.q6 === undefined ? '' : survey.q6,
      q7: survey.q7 === undefined ? '' : survey.q7,
      q8: survey.q8 === undefined ? '' : survey.q8,
      q9: survey.q9 === undefined ? '' : survey.q9,
      q10: survey.q10 === undefined ? '' : survey.q10,
      q11: survey.q11 === undefined ? '' : survey.q11,
      q12: survey.q12 === undefined ? '' : survey.q12,
      q13: survey.q13 === undefined ? '' : survey.q13,
      q14: survey.q14 === undefined ? '' : survey.q14,
      q15: survey.q15 === undefined ? '' : survey.q15,
      q16: survey.q16 === undefined ? '' : survey.q16,
      modalOpen: !this.state.modalOpen,
    });
  }

  fetchServeys() {
    fetch('https://missionaustralia-back.herokuapp.com/getSurveys?token=' + cookies.get('token'))
      .then(response => response.json())
      .then(resData => {
        console.log(resData.data)
        if (resData.response === 200) {
          this.setState({
            surveys: resData.data
          });
        } else {
          alert("Error fetching surveys")
        }
      });
  }

  componentWillMount() {
    if (cookies.get('token') !== undefined && cookies.get('token') !== null) {
      this.setState({
        isLoggedIn: true
      });
    }
  }

  componentDidMount() {
    this.fetchServeys()
  }

  render() {
    if (this.state.isLoggedIn) {
      return (
        <div className="animated fadeIn">
          <Row>
            <Col>
              <Card>
                <CardHeader>
                  <i className="fa fa-align-justify"></i> Chats List
              </CardHeader>
                <CardBody>
                  <Table hover bordered striped responsive size="sm">
                    <thead>
                      <tr>
                        <th>Name</th>
                        <th>Age</th>
                        <th>Email</th>
                        <th>Details</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.surveys.map((survey, i) => {
                        return (
                          <tr key={survey._id}>
                            <td>{survey.name}</td>
                            <td>{survey.age}</td>
                            <td>{survey.email}</td>
                            <td>
                              <a  style={{cursor: 'pointer'}} onClick={()=>{this.toggleModal(survey)}}>Details</a>
                            </td>
                          </tr>);
                      })}
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Modal isOpen={this.state.modalOpen} toggle={this.toggleModal}
          className={'modal-primary ' + this.props.className}>
          <ModalHeader>Chat </ModalHeader>
          <ModalBody>
            <table style={{width: '100%'}}>
              <tbody>
                <tr>
                  <td><b>Full name : </b></td>
                  <td>{this.state.name}</td>
                </tr>
                <tr>
                  <td><b>Age : </b></td>
                  <td>{this.state.age}</td>
                </tr>
                <tr>
                  <td><b>Email : </b></td>
                  <td>{this.state.email}</td>
                </tr>
                <tr>
                  <td><b>Are you working currently? : </b></td>
                  <td>{this.state.q1}</td>
                </tr>
                <tr>
                  <td><b>Which kind of work do you have? : </b></td>
                  <td>{this.state.q2}</td>
                </tr>
                <tr>
                  <td><b>Are you facing any financial crisis currently? : </b></td>
                  <td>{this.state.q3}</td>
                </tr>
                <tr>
                  <td><b>If it is, can you briefly describe about your current financial situation? : </b></td>
                  <td>{this.state.q4}</td>
                </tr>
                <tr>
                  <td><b>Are you living alone or with family? : </b></td>
                  <td>{this.state.q5}</td>
                </tr>
                <tr>
                  <td><b>In which kind of property you are living? : </b></td>
                  <td>{this.state.q6}</td>
                </tr>
                <tr>
                  <td><b>Are you working currently? : </b></td>
                  <td>{this.state.q7}</td>
                </tr>
                <tr>
                  <td><b>Do you have any accommodation problem? : </b></td>
                  <td>{this.state.q8}</td>
                </tr>
                <tr>
                  <td><b>Where you were born, Australia or overseas? : </b></td>
                  <td>{this.state.q9}</td>
                </tr>
                <tr>
                  <td><b>Are you comfortable in communicating English language? : </b></td>
                  <td>{this.state.q10}</td>
                </tr>
                <tr>
                  <td><b>Are you facing any family breakdown problem? : </b></td>
                  <td>{this.state.q11}</td>
                </tr>
                <tr>
                  <td><b>Are you suffering from domestic violence? : </b></td>
                  <td>{this.state.q12}</td>
                </tr>
                <tr>
                  <td><b>Are you facing any health related issue? : </b></td>
                  <td>{this.state.q13}</td>
                </tr>
                <tr>
                  <td><b>Can you briefly describe about your health issue? : </b></td>
                  <td>{this.state.q14}</td>
                </tr>
                <tr>
                  <td><b>Are you addicted to any alcohol or drugs? : </b></td>
                  <td>{this.state.q15}</td>
                </tr>
                <tr>
                  <td><b>Do you faced to any sexual assault in past? : </b></td>
                  <td>{this.state.q16}</td>
                </tr>
              </tbody>
            </table>
          </ModalBody>
        </Modal>

        </div>
      );
    } else {
      return <Redirect to='/login' />
    }
  }
}

export default SurveyList;